<template>
  <div class="container" v-bind:class="{'move': mode === 'move', 'pen':!isEraser && mode === 'normal', 'eraser': isEraser && mode === 'normal'}">
    <div id="sketch">
      <canvas id="paint" ref="paint"></canvas>
    </div>
    <div class="settings">
      <div class="settings-wrap">
        <div class="settings-item">
          Brush Size(Current:{{size}})<input type="range" min="1" max="20" v-model="size" />
        </div>
        <div class="settings-item">
          Color Picker: 
          <!-- <input type="color" v-model="colour"/> -->
          <button style="background:#4d8d67;color:#ffffff;" v-on:click="setColor('#4d8d67')">深绿</button>
          <button style="background:#94c064;color:#ffffff;" v-on:click="setColor('#94c064')">竹林</button>
          <button style="background:#437f95;color:#ffffff;" v-on:click="setColor('#437f95')">松露</button>
          <button style="background:#ff555c;color:#ffffff;" v-on:click="setColor('#ff555c')">冬柿</button>
          <button style="background:#c8457e;color:#ffffff;" v-on:click="setColor('#c8457e')">山葡萄</button>
          <button style="background:#546a92;color:#ffffff;" v-on:click="setColor('#546a92')">月夜</button>
          <button style="background:#5c4c52;color:#ffffff;" v-on:click="setColor('#5c4c52')">山栗</button>
          <button style="background:#667089;color:#ffffff;" v-on:click="setColor('#667089')">冬将军</button>
          <button style="background:#6250b8;color:#ffffff;" v-on:click="setColor('#6250b8')">紫式部</button>
          <button style="background:#4ea6fd;color:#ffffff;" v-on:click="setColor('#4ea6fd')">天蓝</button>
        </div>
        <div class="settings-item">
          Current:{{isEraser ? 'Eraser' : 'Pen'}}
          <button v-on:click="toggleEraser();" v-bind:disabled="!isEraser">Pen</button>
          <button v-on:click="toggleEraser();" v-bind:disabled="isEraser">Eraser</button>
        </div>
        <div class="settings-item">
          Clear All:
          <button v-on:click="clean()">Clear</button>
        </div>
        <div class="settings-item">
          Mode: {{mode}}, {{mode === 'normal' ? 'Press M to Switch Mode' : 'Press N to Switch Mode'}}
          DPR: {{dpr}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {},
  data() {
    return {
      isEraser: false,
      ctx: null,
      backgroundColor: '#ffffff',
      colour: localStorage.colour || '#4d8d67',
      size: localStorage.size || '1',
      mode: 'normal',
      dpr: window.devicePixelRatio || 1
    };
  },
  methods: {
    setColor(colour) {
      this.colour = colour;
    },
    toggleEraser() {
      this.isEraser = !this.isEraser;
    },
    clean() {
      const canvas = this.$refs.paint;
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    }
  },
  watch: {
    colour(newColor) {
      localStorage.colour = newColor;
    },
    size(newSize) {
      localStorage.size = newSize;
    }
  },
  mounted() {
    const canvas = this.$refs.paint;
    const ctx = canvas.getContext('2d');
    // ctx.scale(4, 4);
    
    canvas.style.width = document.documentElement.clientWidth + 'px';
    canvas.style.height = document.documentElement.clientHeight + 'px';
    canvas.width = document.documentElement.clientWidth;
    canvas.height = document.documentElement.clientHeight;
    
    window.addEventListener('resize', () => {
      ctx.save();
      canvas.width = document.documentElement.clientWidth;
      canvas.height = document.documentElement.clientHeight;
      canvas.style.width = document.documentElement.clientWidth + 'px';
      canvas.style.height = document.documentElement.clientHeight + 'px';
      ctx.restore();
    })

    let mouse = {x: 0, y: 0};
    let down = false;

    canvas.addEventListener('mousedown', (e) => {
      if (this.mode === 'move') return;
      down = true;  
      mouse.X = e.pageX;
      mouse.Y = e.pageY;
      ctx.strokeStyle = this.isEraser ? this.backgroundColor : this.colour;
    }, false);
    canvas.addEventListener('mouseup', () => {
      down = false;      
    }, false);
    canvas.addEventListener('mouseout', () => {
      down = false;      
    }, false);
    canvas.addEventListener('mousemove', (e) => {
      if(down) {
        ctx.beginPath();
        ctx.moveTo(mouse.X, mouse.Y);
        ctx.lineCap = 'round';
        ctx.lineWidth = this.size / (this.dpr);
        // ctx.quadraticCurveTo(mouse.X * 2, mouse.Y * 2, e.pageX * 2, e.pageY * 2);
        ctx.lineTo(e.pageX, e.pageY);
        ctx.stroke();
        mouse.X = e.pageX;
        mouse.Y = e.pageY;
      }
    }, false);
    window.addEventListener('keydown', (e) => {
      if (e.keyCode === 77) {
        // 移动模式
        this.mode = 'move';
      } else if (e.keyCode === 78) {
        // 默认模式
        this.mode = 'normal';
      }
    })
  }
}
</script>

<style scoped>
